// Global styling for this template
body {
  overflow-x: hidden;
  @include body-font;
}

p {
  line-height: 1.75;
}

a {
  color: $primary;
  &:hover {
    color: darken($primary, 10%);
  }
}

.text-primary {
  color: $primary !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  @include heading-font;
}

hr{
    border: 3px solid $secondary;
    max-width: 50px;
    margin-bottom: 30px;
}

.page-section {
  padding: 100px 0;
  h2.section-heading {
    font-size: 40px;
    margin-top: 0;
    margin-bottom: 20px;
  }
  h3.section-subheading {
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 35px;
    text-transform: none;
    @include serif-font;
  }
}
@media(min-width:768px) {
  section {
    padding: 150px 0;
  }
}

// Highlight color customization
::-moz-selection {
  background: $primary;
  text-shadow: none;
}

::selection {
  background: #c5d3ff;
  text-shadow: none;
}

img::selection {
  background: transparent;
}

img::-moz-selection {
  background: transparent;
}

.subsection{
  padding: 30px 0px 30px 0px;
  margin-top: 20px;
}

#consulta_publica_ajuda{
  ol{
      margin-bottom: 50px;
    li{
      padding: 0px;
      margin: -6px;
    }
  }
}

#consulta_publica_intro{
  padding-bottom: 0px;
}

#foot{
  background: url(../img/rodape.png) !important;
  padding: 30px;
  img{
    height: 50px;
    opacity: 0.9;    
    &:hover{
      opacity: 1;
    }    
  }
}

#consulta_publica_intro #texto_inicial{
  background-image: url(../img/daflogo.png);
  background-size: 800px;
  background-repeat: no-repeat;
  background-position: center;
}
